import uniSwiperDot from '@/components/uni-swiper-dot/uni-swiper-dot.vue';
import sPopup from '@/components/s-popup/index.vue';
import openAlert from '@/components/open-alert/open-alert';
export default {
	components: { uniSwiperDot, openAlert,sPopup },
	data() {
		return {
			AlertClass: 5,
			AlertPosition: '',
			times: ['一学期', '一年'],
			currentIndex: 0,
			currentGoodIndex: 0,
			number: 0,
			cartNumber:4,
			visible: false,
			showVisible: false,
			dotStyle: true,
			current: 0,
			mode: 'nav',
			tip: '租赁该物品需交押金￥100.00元',
			titleIcon: '[洗涤]',
			descTitle: '澳洲羊毛双层控温被加厚保暖羊毛冬被单双人保暖棉被褥被芯',
			tipListDetail: [
				'1.当您作为消费者通过下单并支付租赁款的情况下，租赁款支付成功即视为您与销售商之间就已支付租赁款部分的订单成立合同关系。',
				'1.当您作为消费者通过下单并支付租赁款的情况下，租赁款支付成功即视为您与销售商之间就已支付租赁款部分的订单成立合同关系。',
				'1.当您作为消费者通过下单并支付租赁款的情况下，租赁款支付成功即视为您与销售商之间就已支付租赁款部分的订单成立合同关系。'
			],
			info: [{ content: '' }, { content: '' }, { content: '' }, { content: '' }, { content: '' }, { content: '' }],
			swiperList: [
				{
					id: 0,
					type: 'image',
					url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big84000.jpg'
				},
				{
					id: 1,
					type: 'image',
					url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big37006.jpg'
				},
				{
					id: 2,
					type: 'image',
					url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big39000.jpg'
				},
				{
					id: 3,
					type: 'image',
					url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg'
				},
				{
					id: 4,
					type: 'image',
					url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big25011.jpg'
				},
				{
					id: 5,
					type: 'image',
					url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big21016.jpg'
				}
			]
		};
	},
	onLoad(option) {
		// console.log(option.url)
		// this.toUrl = option.url;
		// uni.setNavigationBarTitle({
		// 	title: option.title
		// });
	},
	methods: {
		open(Class, Position) {
			this.$nextTick(function() {
				this.AlertClass = Class;
				this.AlertPosition = Position;
				this.$nextTick(function() {
					this.$refs.openAlert.Show();
				});
			});
		},
		change(e) {
			this.current = e.detail.current;
		},
		toBuy() {
			this.visible = true;
		},
		showTip() {
			this.showVisible = true;
		},
		itemGoodClick(index) {
			this.currentGoodIndex = index;
			// this.$emit('selectItem', index);
		},
		plus() {
			this.number++;
		},
		decre() {
			if (this.number > 0) {
				this.number--;
			}
		},
		closeMark() {
			this.visible = false;
		},
		closeRead(){
		this.$nextTick(function() {
		this.$refs.openAlert.Close();
		});
		},
		addToCart(){
			this.cartNumber++
		},
		back(){
			uni.switchTab({
				url:'../index'
			})
		}
	}
};
